<template>
  <div class="iboot-tech-loading"></div>
</template>
<script lang="ts">
import lottie from 'lottie-web'
import { onMounted } from 'vue'
import datajson from '../assets/lottie/loading.json'
export default {
  name: 'Loading',
  props: {},
  setup(): any {
    onMounted(() => {
      lottie.loadAnimation({
        container: document.getElementsByClassName('iboot-tech-loading')[0], // dom
        renderer: 'svg', // 
        loop: true, // 
        autoplay: true, // 
        animationData: datajson // json
      })
    })
  }
}
</script>

<style lang="less">
.iboot-tech-loading{
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  svg {
    width: 260px !important;
    height: 260px !important;
  }
}
</style>
